.checkmark {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
}

.checkbox__input{
    display: none;
}

.checkbox__box{
    width: 1.5em;
    height: 1.5em;
    border: 2px solid #771a1a;
    border-radius: 3px;
    display: flex;
    align-items : center;
    justify-content: center;
    margin-right: 10px;
    flex-shrink: 0;
    transition: all 0.5s ease;
    position: relative;
}

.checkbox__box::after{
    content: "\2714";
    color:rgb(255, 255, 255);
    display: none;
    position: absolute;
}

.checkbox__input:checked + .checkbox__box{
    background-color: rgb(219, 210, 155);
    border-color: rgb(12, 12, 92) ;
}

.checkbox__input:checked + .checkbox__box::after{
    display: block;
}

